<template>
    <div id="modifFourniture">
        <!-- les liens s'adapteront à l'utilisateur connecté -->
        <nav class="navbar navbar-expand-md navbar-light">
            <span class="navbar-brand mb-0 h1">Gestion des fournitures scolaires</span>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="accueil">Accueil</a>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Consulter
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="inv-ele">Inventaire d'un élève</a>
                            <a class="dropdown-item" href="inv-ens">Inventaire d'un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Distribuer
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="distr-cla">A une classe</a>
                            <a class="dropdown-item" href="distr-ele">A un élève</a>
                            <a class="dropdown-item" href="distr-ens">A un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item active">
                        <a class="nav-link" href="gerer-fourniture">Gérer stock de fourniture</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-categorie">Gérer les catégories</a>
                    </li>
                </ul>
                <p class="utilisateur">Nom de l'utilisateur connecté</p>                
                <a href="form_conn" class="btn btn-danger" role="button">Déconnexion</a>
            </div>
        </nav>

                <div class="container-fluid col-md-11">



            <h1>Modifier une fourniture</h1>


            <form action="#" class="row justify-content-md-center top-buffer">

                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer">
                    <div class="col-md-2">
                        <label for="designation">Désignation :</label>
                    </div>
                    <div class="col-md-8 ">
                        <input type="text" id="designation" class="form-control" value="TQG">
                    </div>
                </div>
                    

                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer">
                    <div class="col-md-2">
                        <label for="prix">Prix :</label>
                    </div>
                    <div class="col-md-8 ">
                        <input id="prix" class="form-control form-control-quantite" value="56">
                    </div>
                </div>
                
                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer" title="Si coché, l'enseignant devra saisir une description lors de la distribution">
                    <div class="col-md-2">
                        <label for="aSaisir">Description nécessaire :</label>
                    </div>
                    <div class="col-md-8 ">
                        <input type="checkbox" id="aSaisir" class="form">
                    </div>
                </div>

                <div class="table-responsive top-buffer">
                    <table class="table table-striped">
                        <thead class="thead-dark">
                            <tr>
                                <th scope="col">Catégorie <i class="fa fa-chevron-down"></i></th>
                                <th scope="col" width="50"></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td scope="row" id="col_fourniture">Culture générale</td>
                                <td><input type="checkbox" checked class="form-check-input position-static"></td>
                            </tr>
                            <tr>
                                <td scope="row" id="col_fourniture">Outillage</td>
                                <td><input type="checkbox" class="form-check-input position-static"></td>
                            </tr>
                            <tr>
                                <td scope="row" id="col_fourniture">Informatique</td>
                                <td><input type="checkbox" class="form-check-input position-static"></td>
                            </tr>
                            <tr>
                                <td scope="row" id="col_fourniture">Micromécanique</td>
                                <td><input type="checkbox" class="form-check-input position-static"></td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div class="col-md-12 row justify-content-md-center top-buffer">
                    <div class="col-md-2">
                        <a href="gerer-fourniture" class="btn btn-primary">Annuler</a>
                    </div>
                    <div class="col-md-2">
                        <button type="submit" class="btn btn-primary">Enregistrer</button>
                    </div>
                </div>


            </form>

        </div>


    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'modifFourniture',
    data() {
        return {
            fournitures: [],
            errors: []
        }
    },
    created() { 
        // Récupération des fournitures
        axios.get(`http://localhost:3000/fourniture`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.fournitures = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
    }

}

</script>
<!-- styling for the component -->
<style scoped>
.form-control-quantite {
    min-width: 0;
    max-width: 120px;
    display: inline;
}

form {
    max-width: 900px;
    margin: auto;
}

.top-buffer { 
    margin-top: 20px;
}

.btn-primary {
    margin-top: 10px;
}

table {
    max-width: 500px;
    margin: auto;
}
</style>


